<!-- 
  As I never worked with sitemaps before, I had some issues with
  generating a sitemap. I made some research online and I found this
  method which generates the links from vue-router.
  I followed this tutorial (https://zigamiklic.com/simple-sitemap-for-vue-router/)
  to generate links from router.ts, then after accessing the array I linked
  it through data to the <a> </a> tag using v-bind.
-->

<template>
  <div class="container" id="footer">
    <p>
      <a v-bind:href="Home">Home</a> |
      <a v-bind:href="Categories">Categories</a> |
      <a v-bind:href="Random">Random</a>
    </p>

    <p>Created by Nicoletta Radice for DTT</p>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";

@Options({
  data() {
    return {
      Home: "",
      Categories: "",
      Random: ""
    };
  },

  //  get routes from vue-router
  created() {
    function getRoutesList(routes: any, pre: any) {
      return routes.reduce((array: any, route: any) => {
        const path = `${pre}${route.path}`;

        if (route.path !== "*") {
          array.push(path);
        }

        if (route.children) {
          array.push(...getRoutesList(route.children, `${path}/`));
        }

        return array;
      }, []);
    }

    // access the array
    const routeList = getRoutesList(this.$router.options.routes, "");
    const home = routeList[0];
    this.Home = home;
    const categories = routeList[1];
    this.Categories = categories;
    const random = routeList[2];
    this.Random = random;

    // console.log(categories)
  }
})
export default class Footer extends Vue {}
</script>

<style scoped lang="scss">
#footer {
  display: grid;
  background-color: rgb(85, 125, 235);
  border-top: 1px solid white;
  width: 100%;
  height: auto;
  margin-bottom: 5vh;
  margin-top: 2vh;
  color: white;
}

a {
  color: white;
}
</style>
