<template>
  <div class="news">
    <!-- date sorting button -->
    <button class="button" @click="sortBy()" id="sort">oldest first</button>
    <div class="container">
      <div class="article" v-for="item in showTen" v-bind:key="item.title">
        <p class="title">{{ item.title }}</p>
        <p class="description">{{ item.description }}</p>
        <hr />
        <router-link :to="'/item/' + item.title" id="read-more"
          ><p>read more</p></router-link
        >
        <img v-bind:src="item.urlToImage" alt="News article image" />
        <p class="author">{{ item.author }}</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import { mapState } from "vuex";

@Options({
  props: {
    msg: String
  },

  data() {
    return {
      oldest: false,
      limit: 10
    };
  },
  //get api array
  mounted() {
    this.$store.dispatch("loadNews");
  },
  //get api array
  computed: {
    ...mapState(["news"]),
    showTen() {
      return this.limit ? this.news.slice(0, this.limit) : this.news;
    }
  },

  //get the last news items and put them first
  methods: {
    sortBy() {
      if (this.oldest === false) {
        const reversed = this.news.reverse().slice(10, 20);
        this.news = reversed;
        this.oldest = !this.oldest;
      } else {
        this.news = this.news.reverse();
      }
    }
  }
})
export default class Landing extends Vue {
  msg!: string;
}
</script>

