
import { Options, Vue } from "vue-class-component";
import { mapState } from "vuex";

@Options({
  props: {
    msg: String
  },

  data() {
    return {
      oldest: false,
      limit: 10
    };
  },
  //get api array
  mounted() {
    this.$store.dispatch("loadNews");
  },
  //get api array
  computed: {
    ...mapState(["news"]),
    showTen() {
      return this.limit ? this.news.slice(0, this.limit) : this.news;
    }
  },

  //get the last news items and put them first
  methods: {
    sortBy() {
      if (this.oldest === false) {
        const reversed = this.news.reverse().slice(10, 20);
        this.news = reversed;
        this.oldest = !this.oldest;
      } else {
        this.news = this.news.reverse();
      }
    }
  }
})
export default class Landing extends Vue {
  msg!: string;
}
