<template>
  <!-- details section -->
  <div class="container" id="container-details">
    <a href="/"><button class="button">go back</button></a>
    <div class="article">
      <p class="title">{{ selectedItem.title }}</p>
      <p class="description">{{ selectedItem.description }}</p>
      <img v-bind:src="selectedItem.urlToImage" alt="News article image" />
      <p class="content">{{ selectedItem.content }}</p>
      <p class="author">{{ selectedItem.author }}</p>
    </div>

    <!-- older news section -->
    <div class="load_more">
      <h2>Older news:</h2>
      <div
        class="article"
        id="article-details"
        v-for="item in showThreeMore"
        v-bind:key="item.title"
      >
        <p class="title">{{ item.title }}</p>
        <p class="description">{{ item.description }}</p>
        <img v-bind:src="item.urlToImage" />
        <p class="content">{{ item.content }}</p>
        <p class="author">{{ item.author }}</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import Landing from "./Landing.vue";
import { mapState } from "vuex";

@Options({
  components: {
    Landing
  },

  data() {
    return {
      selectedItem: [],
      limit: 3,
      selected: ""
    };
  },

  //get array from store
  mounted() {
    this.$store.dispatch("loadNews");
  },

  computed: {
    //get array from store
    ...mapState(["news"]),

    //show three more items - first find the index of the post,
    //then slice the array to show three more posts
    showThreeMore() {
      console.log("show 3 more posts");
      return this.limit
        ? this.news.slice(this.selected + 1, this.selected + 4)
        : this.news;
    }
  },

  created() {
    const itemTitle = this.$route.params.itemTitle;
    const selectedItem = this.news.find(
      (item: any) => item.title === itemTitle
    );
    this.selectedItem = selectedItem;
    const selected = this.news.indexOf(this.selectedItem);
    this.selected = selected;
    console.log(selected);
  }
})
export default class Details extends Vue {}
</script>

<style scoped lang="scss">
#container-details {
  display: grid;
}
</style>
